import React, {useState} from "react";
import styles from './TestNouns.module.css'
import data from '../../../Data/Nouns'
import TestSettings from "../../../Components/TestSettings/TestSettings";
import Test from "../../../Components/Test/Test";

function TestNouns(props) {

    const [from, setFrom] = useState(1);
    const [to, setTo] = useState(2);

    const nouns = data.filter(noun => noun.learned);

    return (
        <div className={styles.testNouns}>
            <TestSettings
                from={from}
                setFrom={setFrom}
                to={to}
                setTo={setTo}/>
            <Test words={nouns} from={from} to={to}/>
        </div>
    )
}

export default TestNouns;